.import-orders-table {
  font-size: 12px;
  margin-top: 30px;
  .title {
    text-align: center;
    font-size: 16px;
    color: red;
    margin: 20px;
  }
  .success-message {
    color: green;
    padding: 15px;
    background: white;
    box-shadow: 0px 0px 5px #efefef;
  }
  .tabs-container {
    display: flex;
    align-items: center;
    justify-content: space-around;
    border: 1px solid #cfcfcf;
    .import-tab {
      width: 100%;
      text-align: center;
      padding: 10px;
    }
    .active {
      background: white;
      font-weight: 600;
      border: 1px solid #cfcfcf;
    }
  }
  .table {
    width: 500px;
    background: white;
    box-shadow: 0px 0px 5px #efefef;
    tr {
      th,
      td {
        padding: 10px 0px;
        border-bottom: 1px solid #cfcfcf;
        text-align: center;
      }
      .slno {
        width: 50px;
      }
    }
  }
}
